import {Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {Project} from 'src/app/model/project.model';
import {ProjectSkillService} from 'src/app/services/project-skill.service';
import {LanguageService} from '../../services/language.service';
import {Skill} from '../../model/Skill';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./css/home.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class HomeComponent implements OnInit
{

  readonly projects: Project[];
  readonly skills: Skill[];
  constructor(private workService: ProjectSkillService,
              public languageService: LanguageService)
  {
    this.projects = this.workService.getProjects();
    this.skills = this.workService.getSkills();
  }

  ngOnInit(): void
  {

  }

  addClassToDiv(e: ElementRef): void
  {
    e.nativeElement.classList.add('in-view');
  }

}
